import { useState } from 'react';
import PropTypes from 'prop-types'
import st from './form.module.css'


function Form({ onSubmit }) {
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');

const handleChange = e => {
    const { name, value } = e.target;

    switch (name) {
      case 'name':
        setName(value);
        break;

      case 'number':
        setNumber(value);
        break;

      default:
        return;
    }
  };

const handleSubmit = e => {
    e.preventDefault();
    onSubmit(name, number);
    resetInput();
  };

  const  resetInput = () => {
    setName('');
    setNumber('');
  };
    return (
      <form className={st.TaskEditor} onSubmit={handleSubmit}>
        <label className={st.TaskEditor_label}>
          Name
          <input
            className={st.TaskEditor_input}
            type="text"
            name="name"
            value={name}
            onChange={handleChange}
            placeholder="Ivan Pupkin"
            
          />
        </label>
        <label className={st.TaskEditor_label}>
          Number
          <input
            className={st.TaskEditor_input}
            type="tel"
            name="number"
            value={number}
            onChange={handleChange}
            placeholder="+3809811111111"
            
          />
        </label>
        <button className={st.TaskEditor_button} type="submit">
          Add contact
        </button>
      </form>
    );
  }


Form.propTypes = {
  
  onSubmit: PropTypes.func.isRequired,
};
export default Form;